@font-face {
  font-family: "Roboto";
  font-weight: normal;
  font-style: normal;
  src: url("fonts/Roboto-Regular.eot");
  src: url("fonts/Roboto-Regular.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto-Regular.woff") format("woff"), url("fonts/Roboto-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: url("fonts/Roboto-Medium.eot");
  src: url("fonts/Roboto-Medium.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto-Medium.woff") format("woff"), url("fonts/Roboto-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  src: url("fonts/Roboto-Bold.eot");
  src: url("fonts/Roboto-Bold.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto-Bold.woff") format("woff"), url("fonts/Roboto-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Roboto";
  font-weight: 900;
  font-style: normal;
  src: url("fonts/Roboto-Black.eot");
  src: url("fonts/Roboto-Black.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto-Black.woff") format("woff"), url("fonts/Roboto-Black.ttf") format("truetype");
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  stroke: currentColor;
  stroke-width: 0;
}

.icon-rub {
  font-size: 3.6rem;
  width: 0.916666666666667em;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.l-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.l-wrapper {
  max-width: 990px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
  height: 100%;
  position: relative;
}

.l-map {
  flex-grow: 1;
  pointer-events: none;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  min-width: 32rem;
  line-height: 1.2;
  overflow-x: hidden;
}

button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

button:focus {
  outline: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

[hidden] {
  display: none;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

#svg-sprite {
  position: absolute;
  width: 0;
  height: 0;
}

.btn-route {
  width: 100%;
  max-width: 240px;
  height: 80px;
  border-radius: 14px;
  opacity: 0.85;
  color: #FFFFFF;
  font-size: 45px;
  font-weight: 400;
  letter-spacing: 1.35px;
  text-align: left;
  padding-left: 90px;
  background-repeat: no-repeat;
  background-position: 25px center;
}

.btn-route--short {
  max-width: 180px;
  height: 60px;
  font-size: 35px;
  letter-spacing: 1.05px;
  padding-left: 70px;
  background-size: 30px;
}

.btn-route--bus {
  background-color: #FC3D0C;
  opacity: 0.7;
  background-image: url("images/svg/bus.svg");
}

.btn-route--trol {
  background-color: #2C66D1;
  opacity: 0.85;
  background-image: url("images/svg/trol.svg");
}

.btn-search {
  width: 100%;
}

.btn-search__button {
  width: 100%;
  height: 80px;
  border-radius: 14px;
  background-color: #084AC4;
  color: #FFFFFF;
  font-size: 40px;
  font-weight: 400;
}

.btn-back__button {
  width: 46px;
  height: 74px;
  opacity: 0.25;
  background-image: url("images/svg/arrow.svg");
  background-size: contain;
}

.empty {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  align-items: center;
  padding: 35px 0;
}

.empty__icon {
  position: relative;
  width: 144px;
  height: 144px;
  border-radius: 14px;
  border: 4px solid #084AC4;
  background-repeat: no-repeat;
  box-sizing: border-box;
  background-image: url("images/svg/empty.svg");
  background-position: 40px 15px;
  margin-right: 50px;
}

.empty__icon::after {
  content: "Нет маршрутов";
  position: absolute;
  bottom: 15px;
  left: 0;
  right: 0;
  text-align: center;
  color: #084AC4;
  font-size: 18px;
  font-weight: 500;
}

.empty__text {
  color: #134CB5;
  font-size: 35px;
  font-weight: 700;
  letter-spacing: 1.05px;
  line-height: 50px;
}

.header {
  position: relative;
  z-index: 1;
  box-shadow: 0 5px 10px rgba(0,0,0,0.4);
  background-color: #E0E8F9;
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}

.header__date {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  color: #658ED9;
  font-size: 45px;
  font-weight: 700;
}

.header__date:first-letter {
  text-transform: uppercase;
}

.header__time {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  color: #083B9A;
  font-size: 50px;
  font-weight: 900;
}

.header__pulse {
  animation: pulse 1s linear infinite;
}

@-moz-keyframes pulse {
  50% {
    color: #083B9A;
  }

  51% {
    color: rgba(8,59,154,0);
  }

  100% {
    color: rgba(8,59,154,0);
  }
}

@-webkit-keyframes pulse {
  50% {
    color: #083B9A;
  }

  51% {
    color: rgba(8,59,154,0);
  }

  100% {
    color: rgba(8,59,154,0);
  }
}

@-o-keyframes pulse {
  50% {
    color: #083B9A;
  }

  51% {
    color: rgba(8,59,154,0);
  }

  100% {
    color: rgba(8,59,154,0);
  }
}

@keyframes pulse {
  50% {
    color: #083B9A;
  }

  51% {
    color: rgba(8,59,154,0);
  }

  100% {
    color: rgba(8,59,154,0);
  }
}

.route-detail {
  flex-grow: 1;
  margin-top: 30px;
  overflow: hidden;
}

.route-detail__body {
  padding: 30px 0;
  max-height: 100%;
  box-sizing: border-box;
}

.route-detail__list {
  margin: 0 150px;
  border-left: 8px solid #084AC4;
}

.route-detail__item {
  position: relative;
  padding: 10px 50px;
  color: #000000;
  font-size: 30px;
  font-weight: 400;
}

.route-detail__item::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 5px solid #FFFFFF;
  background-color: #084AC4;
}

.route-detail__item.b-active {
  color: #084AC4;
  font-size: 35px;
  font-weight: 700;
}

.route-detail__item:first-child, .route-detail__item:last-child {
  color: #084AC4;
  font-size: 45px;
  font-weight: 700;
}

.route-detail__item:first-child::before, .route-detail__item:last-child::before {
  left: -36px;
  width: 64px;
  height: 64px;
  border: 8px solid #FFFFFF;
  background-color: #084AC4;
}

.route-detail__item:first-child {
  position: relative;
  top: -20px;
}

.route-detail__item:last-child {
  position: relative;
  bottom: -20px;
}

.route-header--trol {
  background: linear-gradient(to right, #2C66D1 0px, #2C66D1 50%, #E4EDFC 50%, #E4EDFC 100%);
}

.route-header--bus {
  background: linear-gradient(to right, #FD8161 0px, #FD8161 50%, #E4EDFC 50%, #E4EDFC 100%);
}

.route-header__toggle {
  position: absolute;
  z-index: 1;
  top: calc(100% + 30px);
  right: 45px;
  width: 144px;
  height: 144px;
  border-radius: 14px;
  border: 3px solid #084AC4;
  background-repeat: no-repeat;
  box-sizing: border-box;
}

.route-header__toggle::after {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
  color: #084AC4;
  font-size: 25px;
  font-weight: 500;
}

.route-header__toggle--map {
  background-image: url("images/svg/map.svg");
  background-position: center 10px;
}

.route-header__toggle--map::after {
  content: "На карте";
}

.route-header__toggle--schedule {
  background-image: url("images/svg/schedule.svg");
  background-position: center 15px;
}

.route-header__toggle--schedule::after {
  content: "Списком";
}

.route-header__title {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  height: 150px;
}

.route-header__back {
  word-wrap: break-word;
  padding-left: 1%;
  padding-right: 1%;
  width: 39.66666666666667%;
  color: #FFFFFF;
  font-size: 60px;
  font-weight: 500;
  height: 100%;
  box-sizing: border-box;
  background-size: 46px, 55px;
  background-position: left center, 100px center;
  background-repeat: no-repeat;
  padding: 40px 35px 40px 200px;
}

.route-header--trol .route-header__back {
  background-image: url("images/svg/arrow.svg"), url("images/svg/trol.svg");
  background-color: #2C66D1;
}

.route-header--bus .route-header__back {
  background-image: url("images/svg/bus-back.svg"), url("images/svg/bus.svg");
  background-color: #FD8161;
}

.route-header__total {
  word-wrap: break-word;
  padding-left: 1%;
  padding-right: 1%;
  width: 56.333333333333336%;
  box-sizing: border-box;
  color: #084AC4;
  font-size: 30px;
  font-weight: 400;
  line-height: 45px;
  text-transform: uppercase;
  background-color: #E4EDFC;
  height: 100%;
  padding: 55px 35px;
  position: relative;
}

.route-header__total b {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.route-list {
  flex-grow: 1;
  margin: 20px 0;
  overflow: hidden;
}

.route-list__item {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.route-list__item:not(:first-child) {
  border-top: 3px solid rgba(0,0,0,0.25);
}

.route-list__button {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 27.166666666666668%;
}

.route-list__route {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 48%;
  color: #010101;
  font-size: 30px;
  font-weight: 400;
  letter-spacing: 0.9px;
  line-height: 45px;
  text-transform: uppercase;
}

.route-list__time {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 18.833333333333336%;
  color: #000000;
  font-size: 50px;
  font-weight: 700;
  text-align: right;
}

.route-short__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  padding: 20px 0;
}

.route-short__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 31.333333333333336%;
  padding: 15px 0;
}

.route-short__button {
  display: flex;
  align-items: center;
}

.route-short__time {
  padding-left: 20px;
  color: #000000;
  font-size: 30px;
  font-weight: 700;
}

.search {
  background-color: rgba(8,74,196,0.25);
}

.search__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  justify-content: space-between;
  align-items: center;
  height: 170px;
}

.search__link {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 98%;
}

.search__input {
  width: 900px;
  height: 80px;
  border-radius: 14px;
  background-color: #FFFFFF;
  color: #000000;
  font-size: 37px;
  font-weight: 500;
  line-height: 37px;
  padding: 25px;
  box-sizing: border-box;
  border: 0;
}

.search__input:focus {
  outline: 0;
}

.search__input::placeholder {
  color: #084AC4;
  font-size: 35px;
  font-weight: 400;
}

.search__input:placeholder-shown {
  padding-left: 80px;
  background-image: url("images/svg/search.svg");
  background-repeat: no-repeat;
  background-position: 25px center;
}

.tabs {
  padding: 20px 0;
  box-shadow: 0 6px 8px rgba(0,0,0,0.23);
}

.tabs__button {
  color: #084AC4;
  font-family: Roboto;
  font-size: 30px;
  font-weight: 700;
  border-bottom: 5px solid;
  width: 100%;
  max-width: 270px;
  padding: 20px 0;
  display: inline-block;
  margin-right: 35px;
  cursor: pointer;
}

.tabs__button:not(.b-active) {
  opacity: 0.5;
}

.tabs__list {
  padding: 20px 0;
}

.tabs__item {
  color: #000000;
  font-size: 30px;
  font-weight: 500;
  letter-spacing: 0.9px;
  line-height: 45px;
  padding: 20px 0;
}

.tabs__item:not(:first-child) {
  border-top: 3px solid rgba(0,0,0,0.25);
}
